body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquotebefore,
blockquoteafter {
  content: '';
  content: none;
}
qabefore,
qaafter {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html,
body {
  box-sizing: border-box;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 100%;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.bottom-nav {
  position: fixed;
  bottom: 0px;
}
.balance-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 16px;
  border-bottom: 1px solid #e1e4e7;
}
.balance-list-item__direction {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #2e363e;
}
.balance-list-item__sum {
  display: inline-block;
  font-weight: 500;
}
.balance-list-item__sum_negative {
  color: #f1353b;
}
.balance-list-item__sum_positive {
  color: #34c082;
}
.balance-list-item__sum_neutral {
  color: #2e363e;
}
.balance-list-item__arrow {
  width: 24px;
  height: 24px;
  margin: 0 8px;
  display: inline-block;
  background: url("../img/arrow-forward-long.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.balance-status {
  padding: 17px 16px;
  background-color: #fcfdfe;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e1e4e7;
}
.balance-status__text {
  flex-grow: 1;
  font-size: 18px;
  line-height: 1.22;
  color: #7d8790;
}
.balance-status__sum {
  flex-shrink: 0;
  font-size: 22px;
  font-weight: 500;
  color: #2e363e;
  text-transform: uppercase;
}
.blue-subtitle {
  background-color: #fff;
  padding: 16px;
  color: #3f95ff;
  font-size: 14px;
}
.checkbox {
  margin-right: 6%;
  position: relative;
  display: block;
  width: 18px;
  height: 18px;
  border: 2px solid #a0b0be;
  border-radius: 10%;
  cursor: pointer;
  transition: 0.2s;
}
.checkbox::before,
.checkbox::after {
  content: '';
  display: block;
}
.checkbox::before {
  position: absolute;
  height: 200%;
  width: 200%;
  top: -50%;
  left: -50%;
  z-index: -1;
  border-radius: 50%;
  transition: 0.3s;
}
.checkbox::after {
  height: 100%;
  width: 100%;
  background: url("../img/check.svg") center center no-repeat;
  background-size: cover;
  transform: scale(0);
  transition: 0.2s;
}
.checkbox_checked {
  background: #3f95ff;
  border-color: transparent;
}
.checkbox_checked::after {
  transform: scale(1.3);
}
.chip {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 12px;
  padding-right: 29px;
  height: 28px;
  border-radius: 59px;
  background: #3f95ff;
  margin-right: 8px;
  position: relative;
}
.chip:hover {
  box-shadow: 0 0 5px rgba(0,0,0,0.3);
}
.chip__text {
  height: 20px;
  font-size: 12px;
  line-height: 1.7;
  font-weight: 300;
  color: #fff;
}
.chip__cancel-button {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 4px;
  right: 5px;
  background: url("../img/ic-cancel.svg");
  background-size: cover;
}
.chip__cancel-button:active {
  opacity: 0.7;
}
.chip-add-button {
  width: 80px;
  height: 28px;
  box-sizing: border-box;
  border: 1px dashed #d1d5da;
  border-radius: 59px;
  text-align: center;
  line-height: 26px;
  font-size: 12px;
  font-weight: 300;
  color: #83919f;
  margin-right: 8px;
}
.chip-add-button:hover {
  box-shadow: 0 0 5px RGBA(0, 0, 0, 0.15);
}
.events-item {
