.input {
  width: 100%;
  border: none;
  outline: 0px;
  color: #2e363e;
  border-bottom: 1px solid #e1e4e7;
  box-sizing: border-box;
  padding: 0px;
  padding-bottom: 9px;
  line-height: 1.2;
  font-size: inherit;
}
.input:focus {
  padding-bottom: 8px;
  border-width: 2px;
  border-color: #3f95ff;
}
.floating-label {
  position: absolute;
  top: 0px;
  left: 0px;
  line-height: normal;
  color: rgba(46,54,62,0.32);
  pointer-events: none;
  transition: transform 0.3s, color 0.3s;
  will-change: transform;
  transform-origin: left top;
}
.floating-label_transformed {
  transform: scale(0.75) translateY(-24px);
  color: #818f99;
}
.floating-label_small {
  font-size: 16px;
}
.floating-label_fixed {
  top: -25px;
}
.purchase-participants {
  width: 100%;
  padding: 16px 16px 14px 16px;
  background-color: #fff;
}
.purchase-participants__title {
  font-size: 13px;
  color: #818f99;
  line-height: 1.2;
  height: 16px;
  margin-bottom: 6px;
}
.purchase-participants__list {
  display: flex;
}
.purchase-inputs {
  width: 100%;
