  padding: 18px 16px;
  font-weight: 700;
  color: #2e363e;
  font-size: 16px;
}
.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 18px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup__overlay {
  opacity: 0.36;
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
}
.popup__wrapper {
  position: relative;
  top: 0;
  left: 0;
  z-index: 2000;
  max-width: 280px;
  width: 100%;
  max-height: 100%;
  padding-top: 56px;
  padding-bottom: 56px;
  box-shadow: 0 15px 12px 0 rgba(0,0,0,0.22);
  border-radius: 2px;
}
.popup_without-footer .popup__wrapper {
  padding-bottom: 0;
}
.popup__inner {
  max-height: calc(100vh - 148px);
  overflow-y: scroll;
}
.popup__header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  background-color: #fff;
  height: 56px;
  box-sizing: border-box;
  padding: 0 8px;
  font-size: 0;
  box-shadow: 0 0 2px 0 rgba(0,0,0,0.2);
  border-radius: 2px 2px 0 0;
}
.popup__icon {
  width: 24px;
  height: 24px;
  margin: 0 8px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}
.popup__icon:not(:first-child) {
  margin-right: 16px;
}
.popup__icon_close {
  background-image: url("../img/cross.svg");
}
.popup__title {
  margin: 0 8px;
  color: #2e363e;
  font-size: 1rem;
  font-weight: bold;
}
.popup__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  box-sizing: border-box;
  height: 56px;
  padding: 10px 16px;
  background-color: #fff;
  box-shadow: 0 0 2px 0 rgba(0,0,0,0.2);
  border-radius: 0 0 2px 2px;
  text-align: right;
}
.popup__content {
  background: #fff;
  padding-bottom: 8px;
}
.purchase-info {
