.separator {
  position: relative;
  width: calc(100% + 10px);
  height: 8px;
  margin-left: -5px;
  margin-right: -5px;
  overflow: hidden;
  background: #eef1f5;
  border-bottom: 1px solid rgba(0,0,0,0.08);
  box-shadow: inset 0 1px 2px 0 rgba(0,0,0,0.32);
}
.square-button {
  display: inline-block;
  padding: 10px 12px;
  font-size: 14px;
  text-transform: uppercase;
