.tabs {
  position: relative;
  height: calc(100% - %hpx);
  overflow-y: hidden;
}
.tabs__nav {
  display: table;
  table-layout: fixed;
  width: 100%;
  height: 50px;
  line-height: 50px;
  white-space: nowrap;
  border-bottom: 1px solid #dedede;
}
.tabs__link {
  display: table-cell;
  text-align: center;
  text-transform: uppercase;
  color: #afbac2;
  border-bottom: 2px solid transparent;
  cursor: pointer;
}
.tabs__link.is-active {
  color: #3f95ff;
  border-bottom-color: #3f95ff;
}
.tabs__item {
  display: none;
}
.tabs__item.is-active {
  display: block;
}
.tabs__content {
  height: calc(100% - %h);
  overflow-y: scroll;
}
.top-bar-wrapper {
  position: relative;
  height: 56px;
}
.top-bar {
