}
.top-bar__icon--info {
  background-image: url("../img/info.svg");
}
.top-bar__icon--arrow-forward-gray {
  background-image: url("../img/arrow-forward-gray.svg");
}
.top-bar__icon--arrow-forward-blue {
  background-image: url("../img/arrow-forward-blue.svg");
}
.top-bar__icon--arrow-share {
  background-image: url("../img/arrow-share.svg");
}
.top-bar__icon--more-actions {
  background-image: url("../img/more-actions.svg");
}
